.boxmodel {
  /* width: 400px; */
  /* margin: auto; */
  /* margin-top: 100px; */
  border: 1px solid lightgrey;
  overflow: hidden;
  box-shadow: 2px 2px 1px #aaaaaa;
}
.boxmodel_topbar {
  display: flex;
  background-color: #f0ecec;
}
.boxmodel_title {
  margin: 0;
  background-color: var(--default-color);
  transform: skewX(-24deg);
  /* width: 35%; */
  padding: 5px 5px 5px 30px;
  position: relative;
  right: 12px;
}

.boxmodel_title > * {
  transform: skewX(24deg);
}
.boxmodel_title p {
  margin: 0;
  margin-left: 10px;
  margin-right: 100px;
  font-family: 'Bebas Neue', 'Serif';
  font-size: 25px;
  color: rgba(248, 248, 248, 255);
}
.extra {
  width: 5px;
  background-color: var(--default-color);
  transform: skewX(-24deg);
  position: relative;
  right: 12px;
  margin-left: 5px;
}

.boxmodel_nav {
  display: flex;
  align-items: center;
  margin-left: auto;
}
.arrow {
  margin-right: 30px;
  cursor: pointer;
}
.arrow img {
  width: 8px;
}

.cancel {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  margin-right: 10px;
}
.cancel img {
  cursor: pointer;
}

