.banner {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 40px;
  height: 250px;
  background: url('banner.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.bannertext p {
  font-family: 'Bebas Neue', 'Serif';
  font-size: 36px;
  color: rgba(248, 248, 248, 255);
}
.bannerform input[type='email'] {
  height: 50px;
  width: 350px;
  border-radius: 5px 0px 0px 5px;
  border: none;
  text-align: center;
  color: grey;
}

.bannerform input::placeholder {
  color: lightgrey;
  text-align: center;
  font-size: 12px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
}
.bannerform form {
  display: flex;
  justify-content: space-around;
}
.bannerform button {
  background-color: var(--default-color);
  height: 50px;
  width: 100px;
  font-size: 12px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  color: white;
  border: none;
  border-radius: 0px 15px 15px 0px;
  cursor: pointer;
}
.bannerform input:focus {
  outline: none;
}

